import React from 'react';
import { Typography, Divider, Carousel, Tooltip } from 'antd';
import ImageMapper from '../../components/ImageMapper';
import { landingPage as cms } from '../../cms';
import { landingPageCarousel, altDisAssebly } from '../../config';
const { Title, Paragraph, Text } = Typography;

const alternatorCords = [
  { coords: [148, 272], href: "#alternator_part_8", tooltip: cms["subHeading8"] },
  { coords: [206, 276], href: "#alternator_part_7", tooltip: cms["subHeading7"] },
  { coords: [314, 280], href: "#alternator_part_3", tooltip: cms["subHeading3"] },
  { coords: [315, 197], href: "#alternator_part_6", tooltip: cms["subHeading6"] },
  { coords: [306, 133], href: "#alternator_part", tooltip: cms["subHeading9"] },
  { coords: [415, 283], href: "#alternator_part_4", tooltip: cms["subHeading4"] },
  { coords: [476, 284], href: "#alternator_part_3", tooltip: cms["subHeading3"] },
  { coords: [417, 107], href: "#alternator_part_", tooltip: cms["subHeading10"] },
  { coords: [517, 46], href: "#alternator_part_1", tooltip: cms["subHeading1"] },
  { coords: [461, 202], href: "#alternator_part_2", tooltip: cms["subHeading2"] }
]

const responsiveSlabs = [
  { w: 848, r: 8 },
  { w: 748, r: 7 },
  { w: 648, r: 6 },
  { w: 548, r: 5 },
  { w: 448, r: 5 },
  { w: 398, r: 5 },
  { w: 348, r: 4 },
  { w: 298, r: 4 },
  { w: 248, r: 3 },
  { w: 198, r: 2 },
]

const CONTAINER_HEIGHT = 524;
const CONTAINER_WIDTH = 700;
const DOT_RADIUS = 8;

class LandingPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      height: CONTAINER_HEIGHT,
      width: CONTAINER_WIDTH,
      xFactor: 1,
      yFactor: 1,
      dotRadius: DOT_RADIUS,
    }
  }

  componentDidMount() {
    responsiveSlabs.forEach(slab => {
      window.matchMedia(`(max-width: ${slab.w}px)`).addListener(this.reponsiveScreenHandler(slab.r))
    })
    const currentWidth = document.getElementById('root').offsetWidth;
    const dotRadius = responsiveSlabs.find(slab => currentWidth > slab.w).r;
    this.updateImgDimensions(currentWidth, dotRadius);
  }

  reponsiveScreenHandler = r => e => {
    let a = document.getElementById('root');
    const currentWidth = a.offsetWidth;
    this.updateImgDimensions(currentWidth, r);
  }

  updateImgDimensions = (currentWidth, dotRadius = 8) => {
    if (currentWidth > 848) {
      this.setState({ height: CONTAINER_HEIGHT, width: CONTAINER_WIDTH, xFactor: 1, yFactor: 1, dotRadius: 8 });
    } else {
      const width = currentWidth - 148;
      const height = (CONTAINER_HEIGHT / CONTAINER_WIDTH) * width;
      const xFactor = width / CONTAINER_WIDTH;
      const yFactor = height / CONTAINER_HEIGHT;
      this.setState({ width, height, xFactor, yFactor, dotRadius });
    }
  }

  displayPartsDetails = () => {
    return [1, 2, 3, 4, 5, 6, 7, 8].map(value => (
      <React.Fragment key={value}>
        <Title id={`alternator_part_${value}`} level={4} underline>{cms[`subHeading${value}`]}</Title>
        <Paragraph>{cms[`subText${value}`]}</Paragraph>
      </React.Fragment>
    ))
  }

  getMappingCoordinates = () => {
    const { dotRadius, xFactor, yFactor } = this.state;
    return {
      areas: alternatorCords.map(({ coords, href, tooltip }) => {
        let [x, y] = coords;
        x = x * xFactor;
        y = y * yFactor;
        return {
          shape: 'circle',
          coords: [x, y, dotRadius],
          preFillColor: "red",
          href,
          tooltip,
        }
      }),
      name: 'abc'
    }
  }

  render() {
    const { height, width } = this.state;

    return (
      <Typography>
        <Title>
          {cms.title}
        </Title>
        <Carousel autoplay>
          {landingPageCarousel.map(imageUrl => <img src={imageUrl} key={imageUrl} alt="N/A" />)}
        </Carousel>
        <Title level={4} align='center'>{cms.altWorkingHeading}</Title>
        <div id="alt-assembly-outer-div">
          <div style={{ maxHeight: height, maxWidth: width, display: 'block', margin: 'auto' }} >
            <ImageMapper
              src={altDisAssebly}
              height={height}
              width={width}
              map={this.getMappingCoordinates()}
            />
          </div>
        </div>
        <Paragraph>{cms.altPara1}</Paragraph>
        <Paragraph>{cms.altPara2}</Paragraph>
        <Paragraph>{cms.altPara3}</Paragraph>
        {this.displayPartsDetails()}
      </Typography>
    )
  }
}

export default LandingPage;
